.login-page {
    height: 100vh !important;
    width: 100vw !important;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background: rgb(65,96,255);
    background: radial-gradient(circle, rgba(65,96,255,1) 0%, rgba(254,170,187,1) 100%);
  }

  .login-form {
    background-image: linear-gradient(to right, #fbbce4, #f55fb6, #5c0136c5);
    margin-left: 40px;
    margin-right: 80px;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 20px 20px rgba(0, 0, 0, 0.682);
  }

  .login-form h1 {
    color: white;
    text-shadow: 4px 4px 6px #000000;
    text-align: center;
    font-weight: bold;
    margin: 30px;
    
  }
  
  .login-form .ant-form-item-label label {
    font-weight: bold;
    color: #01123e !important;
    
  }
  .login-form button {
    background-color: #f0f3fc;
    color: rgb(0, 0, 0);
    outline: none;
  }
  .login-form button:hover {
    background-color: black;
    color: white;
  }
  .login-form a {
    font-weight: bold;
    color: #030351 !important;
  }
  
  .login-form a:hover {
    color: black !important;
  }
  .login-page .row.container h1{
    color: white;
    text-shadow: 2px 2px 4px #000000;
    text-align: center;
    font-weight: bold;
    margin: 30px;
    font-size: 4.0rem;
  }
  .login-page .row.container .col-md-6 img{
    border-radius: 8px;
    padding: 5 px;
    width: 80%;
    box-shadow: 20px 20px rgba(0, 0, 0, 0.682);
    object-fit: cover;
    margin-left:150px;
  }
