.navbar {
   
    background-image: linear-gradient(to right, #5c0136c5, #f55fb6, #5c0136c5);
    box-shadow: 0px 10px  rgba(130, 137, 139, 0.335);
  }
  .navbar .navbar-brand {
    color: rgb(248, 218, 181) !important;
    font-weight: bold;
    font-size: 1.7rem;
    text-shadow: 2px 2px 2px #f6f07569;
  }
  
  .nav-link {
    font-size: 1.3rem;
    color:rgb(255, 253, 253);
    font-weight: bold;
    margin-right: 10px;
    margin-top: 3px;
   
  }
  
  .navbar h6 {
    margin-top: 0px;
    margin-right: 20px;
  }
  .nav-item button {
      margin-top: 7px;
  }
  .nav-item button {
    background-color: #ff0000;
    color: #ffffff;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border: #ffffff;
  }
  .nav-item button:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
  }
  